  Josh's Custom CSS Reset
  https://www.joshwcomeau.com/css/custom-css-reset/
*/
*, *::before, *::after {
    box-sizing: border-box;
  }
  * {
    margin: 0;
  }
  body {
    line-height: 1.5;
    -webkit-font-smoothing: antialiased;
  }
  img, picture, video, canvas, svg {
    display: block;
    max-width: 100%;
  }
  input, button, textarea, select {
    font: inherit;
  }
  p, h1, h2, h3, h4, h5, h6 {
    overflow-wrap: break-word;
  }
  #root, #__next {
    isolation: isolate;
  }





  body {
    display: grid;
    min-height: 100vh;
    grid-template-columns: 1fr 4fr;
    grid-template-rows: 100px auto;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Helvetica Neue', sans-serif;
    background-color: #c3c3d8;
    color: #333;
    padding: 2rem;
    transition: background-color 0.3s ease;
    overflow-x: hidden;
    overflow-y: auto;
    -webkit-overflow-scrolling: touch;
    margin: 0;
    padding: 0;

  }

  ul {
    list-style-type: none;
  }

  .header {
    grid-column: 1 / 2;
    grid-row: 1 / 2;
    background-color: #b1c3e6;
    padding: 1rem;
    top: 0;
    height: auto !important;
  }

  .sidebar {
    grid-column: 1 / span 1;
    grid-row: 2 / span 1;
    background-color: #b1c3e6;
    padding: 0;
    margin: 0;
    top: 60px;
    height:auto;
    display: flex;
    flex-direction: column;
    gap: 1rem;
    overflow-x: auto;
    overflow-y: auto;
  }

  .main-content { 
    grid-column: 2 / span 1;
    grid-row: 1 / span 2;
    background-color: #fff;
    padding: 1rem;
    transition: background-color 0.3s ease;
    overflow-x: auto;
    overflow-y: auto;
    transition: background-color 0.3s ease;
    overflow-x: auto;
    overflow-y: auto;
    height:auto;
    display: flex;
    flex-direction: column;
    gap: 1em;
    min-height: 50vh;
  }

  .project-add {
    display: flex;
  }

  .btn-add-project {
    background-color: #007bff;
    color: #fff;
    padding: 0.5rem 1rem;
    border: none;
    border-radius: 7px;
    cursor: pointer;
    font-size: 1rem;
    font-weight: 600;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
    transition: background-color 0.3s ease;
    outline: none;
    position: sticky;
    margin: 1em  ;
    flex: 1;
  }

  .project-list {
    list-style-type: none;
    padding: 0;
    margin: 0;
    overflow-y: auto;
    height: 100%;
    display: flex;
    flex-direction: column;
    height: fit-content;
    position: sticky;
  }

  .project {
    padding: 1em;
    cursor: pointer;
  }

  .project-selected {
    background-color: white;
  }

  .project-add {
    position: sticky;
  }
  
  .new-project-form {
    padding: 1rem;
    position: fixed;
    background-color: #b1c3e6;;
    color: #fff;
    top: 4.5rem;
    left: 1rem;
    overflow-y: auto;
    display: grid;
    grid-template-columns: auto;
    grid-template-rows: auto auto;
    width: 242px;
  }

  .main-content {
    display: flex;
    flex-direction: column;
  }

