    transition: background-color 0.3s ease;
    outline: none;
    margin-top: 1em;
  }

  .todo-list {
    list-style-type: none;
    padding: 0;
    margin: 0;
    overflow-y: auto;
    height: 100%;
    display: flex;
    flex-direction: column;
    gap: 1em;
